<template>
    <MySpace vertical gap="0">
        <h3>
            {{ msg }}你可以点击
            <MyUpload @upload="upload">这里</MyUpload>

            上传你的互动规则
        </h3>
        <MySpace justify="space-between" :style="{ width: width + 'px' }">
            <h3>视频编号 {{ num }}</h3>
            <MySpace>
                <h3>视频路径</h3>
                <h3 v-for="(p, i) in path" :key="i">{{ p }}</h3>
            </MySpace>
        </MySpace>

        <MyVideo
            :src="videos[num].src"
            :p="videos[num].p"
            :width="width"
            :height="height"
        />

        <h3>{{ videos[num].desc ? videos[num].desc : "你的选择是" }}</h3>
        <MySpace>
            <button
                v-for="(option, i) in videos[num].options"
                :key="i"
                @click="(num = option.jump), append(option.jump)"
            >
                {{ option.text }}
            </button>
        </MySpace>
    </MySpace>
</template>

<script>
import MyVideo from "./components/MyVideo.vue";
import MySpace from "./components/MySpace.vue";
import MyUpload from "./components/MyUpload.vue";

export default {
    name: "App",
    components: {
        MyVideo,
        MySpace,
        MyUpload,
    },
    data() {
        return {
            width: "200",
            height: "400",
            num: 0,
            path: [0],
            videos: [
                {
                    src: "BV1kv411P7Sz",
                    desc: "你愿意成为伥鬼吗",
                    options: [
                        {
                            text: "我就是伥鬼本鬼",
                            jump: 1,
                        },
                        {
                            text: "什么是伥鬼",
                            jump: 2,
                        },
                    ],
                },
                {
                    src: "BV15A411c7nc",
                    p: 11,
                    desc: "结局一",
                    options: [
                        {
                            text: "回到开始",
                            jump: 0,
                        },
                    ],
                },
                {
                    src: "BV1ha411672q",
                    options: [
                        {
                            text: "去看看伥鬼现场",
                            jump: 1,
                        },
                    ],
                },
            ],
            msg: "这是一个互动视频的测试，",
        };
    },
    methods: {
        append(num) {
            if (this.path.indexOf(num) >= 0) {
                let i = this.path.indexOf(num);
                this.path.splice(i + 1, this.path.length - i - 1);
            } else {
                this.path.push(num);
            }
        },

        upload(data) {
            this.videos = data;
            this.msg = "";
            setTimeout(() => {
                alert("上传成功");
            }, 100);
        },
    },
    mounted() {
        let max_width = Math.floor(document.body.clientWidth * 0.9);
        let max_height = Math.floor(document.body.clientHeight * 0.7);
        if ((max_width * 9) / 16 < max_height) {
            this.width = max_width;
            this.height = Math.floor((max_width * 9) / 16);
        } else {
            this.height = max_height;
            this.width = Math.floor((max_height * 16) / 9);
        }
        console.log(
            "设备屏幕大小",
            document.body.clientWidth,
            document.body.clientHeight
        );
    },
};
</script>

