<template>
    <button @click="upload" :focusable="false"><slot /></button>
    <input ref="inputRef" v-show="false" type="file" />
</template>

<script setup>
import { ref, onMounted, defineEmits } from "vue";
const emit = defineEmits(["upload"]);
const inputRef = ref(null);

const deal = (e) => {
    if (e.target.files.length == 0) return;
    // 设置模式
    let file = e.target.files[0];

    // 判断文件格式
    if (file.name.search(/.json$/) >= 0) {
        // 读取json
        let reader = new FileReader();
        reader.onload = deal_json;
        reader.readAsText(file);
    } else {
        let ans = "只支持json格式的数据";
        alert(ans);
    }
};

const deal_json = (ev) => {
    let data = JSON.parse(ev.target.result);
    console.log(data);
    emit("upload", data);
};

onMounted(() => (inputRef.value.onchange = deal));

const upload = () => inputRef.value.click();
</script>

